
<template>
    <div class="page">
        <label>Uygun Saatler</label>
        <input type="date" :min="today" v-model="selectedDate" required>

        <p v-if="loading" class="alert info">Yükleniyor...</p>
        <p v-if="errorMessage" class="alert error">{{ errorMessage }}</p>

        <ul v-if="availableSlots && availableSlots.length" class="slot-grid">
            <li v-for="slot in availableSlots" :key="slot">
                <button class="slot" @click="selectedTime = slot" :class="{selected: selectedTime === slot}">
                    {{ slot }}
                </button>
            </li>
        </ul>
        <p v-else-if="selectedDate && !loading && !errorMessage">Bu tarih için uygun saat bulunamadı.</p>

        <p v-if="selectedTime">Seçilen saat: {{ selectedTime }}</p>

        <div v-if="selectedTime" class="field">
            <label>Ad Soyad: </label>
            <input type="text" v-model="name" required>
            <label>Email: </label>
            <input type="text" v-model="email" required>
            <label>Not: </label>
            <textarea name="not" v-model="note"></textarea>
        </div>
        <p v-if="successMessage" class="alert success">{{ successMessage }}</p>

        <button class="primary" @click="createAppointment" :disabled="!selectedTime || loading || !selectedDate || !name || !email">
            Randevu Oluştur
        </button>
    </div>
    
</template>

<script>
import { ref, watch } from 'vue'
import api from '@/services/api'

export default {
    setup() {
        let selectedDate = ref('')
        let selectedTime = ref('')
        let availableSlots = ref([])
        let loading = ref(false)
        let errorMessage = ref('')

        let name = ref('')
        let email = ref('')
        let note = ref('')
        let successMessage = ref('')
        const today = new Date().toISOString().slice(0,10);

        const fetchSlots = () => {
            errorMessage.value = ''
            loading.value = true
            availableSlots.value = []
            console.log("API BASE:", api.defaults.baseURL);

            api.get("/appointments/slots", { params: { date: selectedDate.value } })
  .then(res => {
    console.log("selectedDate:", selectedDate.value);
    console.log("slots:", res.data); // backend ne gönderiyor göreceğiz

    availableSlots.value = res.data;
  })
  .catch(err => {
    console.log("slots error:", err.response?.status, err.response?.data);
  })
  .finally(() => {
    loading.value = false;
  });

        }

        watch(selectedDate, () => {
            fetchSlots()
        })

        const createAppointment = () => {
            errorMessage.value = ''
            successMessage.value = ''
            api.post("/appointments", {
                name: name.value,
                email: email.value,
                note: note.value,
                date: selectedDate.value,
                time: selectedTime.value
            })
            .then(() => {
                successMessage.value = "Randevu oluşturuldu."
                selectedTime.value = ''
                note.value = ''
                fetchSlots()
            })
            .catch(err => {
                if (err.response?.status == 409) { 
                    errorMessage.value = "Bu saat dolu. Lütfen başka bir saat seçiniz." }
                else { errorMessage.value = "Randevu oluşturulamadı." }
                fetchSlots()
            })
        }


        return {selectedDate, availableSlots, loading, errorMessage, selectedTime, 
            name, email, note, successMessage, createAppointment, today}
    }
}
</script>

<style>
.page {
  max-width: 720px;
  margin: 40px auto;
  padding: 16px;
}

.slot-grid {
  display: grid;
  grid-template-columns: repeat(4, minmax(0, 1fr));
  gap: 10px;
  margin-top: 10px;
}

.slot {
  padding: 10px 0;
  border-radius: 12px;
  border: 1px solid #ddd;
  background: #fafafa;
  cursor: pointer;
  transition: transform .08s ease, box-shadow .12s ease, background .12s ease;
}

.slot:hover {
  transform: translateY(-1px);
  box-shadow: 0 6px 14px rgba(0,0,0,.08);
  background: #f3f6ff;
}

.slot.selected {
  border-color: #2f6fed;
  background: #eaf1ff;
  font-weight: 600;
}
.field {
  display: grid;
  gap: 6px;
  margin-top: 12px;
}

label {
  font-size: 13px;
  color: #555;
}

input, textarea {
  width: 100%;
  padding: 10px 12px;
  border-radius: 12px;
  border: 1px solid #ddd;
  outline: none;
  transition: border-color .12s ease, box-shadow .12s ease;
}

input:focus, textarea:focus {
  border-color: #2f6fed;
  box-shadow: 0 0 0 4px rgba(47,111,237,.15);
}
.alert {
  margin-top: 12px;
  padding: 10px 12px;
  border-radius: 12px;
  font-size: 14px;
  border: 1px solid transparent;
}

.alert.info { background: #f6f7f9; border-color: #e5e7eb; color: #374151; }
.alert.error { background: #fff1f2; border-color: #fecdd3; color: #9f1239; }
.alert.success { background: #ecfdf5; border-color: #a7f3d0; color: #065f46; }

.primary {
  margin-top: 14px;
  width: 100%;
  padding: 12px 14px;
  border-radius: 14px;
  border: none;
  background: #2f6fed;
  color: white;
  font-weight: 600;
  cursor: pointer;
  transition: filter .12s ease, transform .08s ease;
}

.primary:hover { filter: brightness(.95); }
.primary:active { transform: translateY(1px); }

.primary:disabled {
  opacity: .5;
  cursor: not-allowed;
  filter: none;
  transform: none;
}

</style>
